import React from "react";
import styled from "styled-components";
import { useAddStudent } from "../../../Context/AddStudent";
const OpenExcelFile = React.lazy(() => import("./OpenExcelFile"));

const ImportDocument = () => {
  const students = useAddStudent();
  return (
    <ImportFile>
      <Title>Choose The File</Title>
      {students.rows.length > 0 && <OpenExcelFile />}
      {students.rows.length < 1 && (
        <Input
          name="oFile"
          type="file"
          onChange={(e) => {
            const file = e.target.files[0];
            students.setXlsxFile(file);
          }}
        />
      )}

      <ButtonPanel>
        <MyButton
          onClick={() => {
            students.handleIsImport(false);
          }}
        >
          Back
        </MyButton>
        {students.array.length > 0 && (
          <MyButton onClick={students.handleImportSubmit}>Upload</MyButton>
        )}
      </ButtonPanel>
    </ImportFile>
  );
};

export default ImportDocument;

const ImportFile = styled.div`
  border: 1px solid rgba(3, 3, 3, 0.3);
  border-radius: 5px;
  padding: 1rem;
  padding-top: 1.5rem;
  width: 600px;
  @media (max-width: 610px) {
    width: 95%;
    padding: 0.5rem;
  }
`;

const Input = styled.input`
  padding: 0.3rem 1rem 0.3rem 0.3rem;
  border: 1px solid rgba(3, 3, 3, 0.5);
  border-radius: 5px;
  margin-top: 1.5rem;
  width: 100%;
`;

const MyButton = styled.button`
  font-weight: 500;
  background: rgba(230, 230, 230, 0.4);
  margin: 0.5rem 0 0.5rem 0.5rem;
  min-width: 100px;

  &:nth-child(1) {
    background: rgba(255, 71, 87, 1);
    color: #fff;
    border-radius: 3px;
  }
  &:nth-child(2) {
    background: rgba(55, 66, 250, 1);
    color: #fff;
    border-radius: 3px;
  }
  &:hover {
    &:nth-child(1) {
      background: rgba(255, 100, 87, 1);
    }
    &:nth-child(2) {
      background: rgba(55, 100, 250, 1);
    }
  }
`;

const ButtonPanel = styled.div`
  display: flex;
  align-items: end;
  justify-content: flex-end;
  margin-top: 0.1rem;
`;

const Title = styled.h1`
  font-size: 1.2rem;
  font-weight: 500;
  text-align: center;
`;
